import React, {useEffect, useState} from "react";
import {Col, ConfigProvider, Row} from 'antd';
import {CourseBoxItem} from "../../types"
import "./index.scss"
import CourseBox from "../../components/CourseBox";
import Constants from "../../common/Constants";


const defaultUrl = `https://developer.huawei.com/consumer/cn/training/course/slightMooc/C101667303102887820`;

const CourseLearning: React.FC = () => {
  const [courses, setCourses] = useState<CourseBoxItem[]>([
    {
      id: "001",
      cover: "assets/course_cover_1.png",
      title: "HarmonyOS",
      brief: "HarmonyOSHarmonyOS",
      duration: 30,
      course_num: 3,
      url: 'https://developer.huawei.com/consumer/cn/training/course/slightMooc/C101667303102887820'
    },
    {
      id: "002",
      cover: "assets/course_cover_2.png",
      title: "DevEco Studio",
      brief: "DevEco Studio",
      duration: 60,
      course_num: 6
    },
    {
      id: "003",
      cover: "assets/course_cover_3.png",
      title: "ArkTS",
      brief: "TSArkTS",
      duration: 60,
      course_num: 2
    },
    {
      id: "004",
      cover: "assets/course_cover_4.png",
      title: "",
      brief: "",
      duration: 45,
      course_num: 2
    },
    {
      id: "005",
      cover: "assets/course_cover_5.png",
      title: "",
      brief: "",
      duration: 90,
      course_num: 6
    },
    {
      id: "006",
      cover: "assets/course_cover_6.png",
      title: "",
      brief: "",
      duration: 90,
      course_num: 4
    },
    {
      id: "007",
      cover: "assets/course_cover_7.png",
      title: "",
      brief: "HTTP",
      duration: 30,
      course_num: 3
    },
    {
      id: "008",
      cover: "assets/course_cover_8.png",
      title: "",
      brief: "",
      duration: 30,
      course_num: 3
    }
  ]);
  const [windowSize, setWindowSize] = useState<number>(0);

  const handleResize = () => {
    setWindowSize(window.innerWidth);
  }

  const handleClick = (course: CourseBoxItem) => {
    window.location.href = course.url || defaultUrl;
  }

  useEffect(() => {
    handleResize();
    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener('resize', handleResize);
  }, [])

  return (
    <ConfigProvider
      theme={{
        token: {
          screenXSMin: 0,
          screenXS: 120,
          screenXSMax: 300,
          screenSMMin: 300,
          screenSM: 300,
          screenSMMax: 600,
          screenMDMin: 600,
          screenMDMax: 840,
          screenLGMin: 840,
        }
      }}
    >
      <div className="course_learing_box">
        {
          windowSize > Constants.LG_SIZE ? (
            <div className="complexBanner">
              <div className="inner_banner">
                <img src="assets/banner.png" alt=""/>
              </div>
              <div className="inner_description">
                <div className="innner_page_title">HarmonyOS</div>
                <div className="inner_brief">
                  ArkTSHarmonyOS
                </div>
              </div>
            </div>
          ) : (
            <>
              <div className="page_title">HarmonyOS</div>
              <div className="banner">
                <img src="assets/banner.png" alt=""/>
              </div>
              <div className="description">
                ArkTSHarmonyOS
              </div>
            </>
          )
        }

        <div className="basic_course"></div>
        <Row
          gutter={[{xs: 4, sm: 8, md: 12, lg: 16}, {xs: 4, sm: 12, md: 12, lg: 16}]}>
          {
            courses.map((course: CourseBoxItem) =>
              <Col
                onClick={() => handleClick(course)}
                xs={24} sm={12} md={6} lg={4}
                key={course.id}
              >
                <CourseBox {...course} data-course={course}></CourseBox>
              </Col>
            )
          }
        </Row>
      </div>
    </ConfigProvider>
  )
}

export default CourseLearning;